import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import { Link } from "react-router-dom";

const SpecializeFilterPane = () => {
  return (
    <Form method="get" className="row gy-2 gx-3 align-items-center p-2">
      <Form.Group className="col-auto">
        <Form.Label className="visually-hidden"> Keyword </Form.Label>
        <Form.Control
          type="text"
          placeholder="Nhp t kha..."
          name="keyword"
        />
      </Form.Group>
      <Form.Group className="col-auto">
        <Form.Label className="form-check-label">Hin th trn Menu</Form.Label>
        <Form.Control
          className="form-check-input"
          type="checkbox"
          name="showOnMenu"
        />
      </Form.Group>
      <Form.Group className="col-auto">
        <Button variant="danger" type="reset">
          Xa lc
        </Button>
        <Link to="/admin/categories/edit" className="btn btn-success ms-2">
          Thm mi
        </Link>
      </Form.Group>
    </Form>
  );
};

export default SpecializeFilterPane;
